@import "../../../styles/breakpoints.scss";

.container {
  width: 1000px;
  margin: 0 auto;
  @include media('<=tablet') {
    width: 100%;
  }
}

.flex {
  display: flex;
  flex-direction: row;
  align-items: center; 
  justify-content: space-between
}

.form {
  padding-top: 0.5em;
}

