@import "../../../../styles/breakpoints.scss";

.container {
  width: 4.6em;
  height: 100%;
  position: fixed;
  overflow: scroll;
  bottom: 0;
  left: 0;
  right: auto;
  background-color: #000000;
  @include media('<=tablet') {
    display: none;
  }
}

.container::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.container {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.sideconstruct {
  margin-top: 6.5em;
  width: 100%;
  margin-bottom: 6.5em;
  overflow: scroll;
}

.sideconstruct::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.sideconstruct {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}