@import "../../../../styles/breakpoints.scss";


.container {
  margin-left: 4.6em;
  height: calc(100% - 6em);
  display: flex;
  display: block;
  @include media('<=tablet') {
    display: none;
  }
}

// ########################## Main panel ##########################
.panel {
  display: block;
  margin-left: 73px;
  background: inherit;
  position: absolute;
  top: 6em;
  right: 0;
  bottom: 6em;
  left: 0;
  @include media('<=tablet') {
    display: none;
  }
}

.sidebar {
  display: flex;
  height: 5.5em;
  background-color: #131313;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}

.sidebar_button {
  width: 4.5em;
  height: 4.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #2e2e2e;
}

.sidebar_button_space {
  padding-left: 0.8em;
}
// ########################## Search panel ##########################
.search {
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0.01em;
  top: 5.5em;
  @include media('<=tablet') {
    display: none;
  }
}

.search[data-state="false"] {
  display: none;
}

.search_panel {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 0.5em;
  select {
    border-radius: 30px 0px 0px 30px;
    padding: 0% 1em;
    color: #ffffff;
    height: 3em;
    font-size: 18px;
    font-family: 'DM Sans';
    background: #313131;
    border: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  input[form-name="search"] {
    justify-content: center;
    align-items: center;
    color: #ffffff;
    width: 84%;
    height: 3em;
    font-size: 18px;
    font-family: 'DM Sans';
    background: #3d3d3d;
    border: 0px;
    padding-left: 1em;
  }
  input[form-name="summit"] {
    justify-content: center;
    align-items: center;
    color: #ffffff;
    height: 3em;
    font-size: 18px;
    font-family: 'DM Sans';
    background: #313131;
    border: 0px;
    padding-left: 1em;
    padding-right: 1em;
    border-radius: 0px 30px 30px 0px;
  }
}

.search_res {
  display: grid;
  height:  calc(100% - 5em);
  overflow-y: scroll;
  overflow-x: hidden;
  margin-top: 0.5em;
  margin-bottom: 10em;
  margin-left: 0.5em;
  margin-right: 0.5em;
  // margin-right: 1%;
  background: inherit;
  img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    aspect-ratio: 1/1;
  }
}

.search_res[data-state="false"] {
  display: none;
}

.search_res::-webkit-scrollbar {
  display: none;
}

.search_res {
-ms-overflow-style: none;
scrollbar-width: none;
}

.search_none {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 98%;
  margin-top: 0.5em;
  margin-left: 1%;
  margin-right: 2%;
  padding-top: 2em;
  padding-bottom: 2em;
  background: #3d3d3d;
  border-radius: 30px;
}

.search_none[data-state="true"] {
  display: none;
}

.no_mem {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 98%;
  margin-top: 1.5em;
  margin-left: 1%;
  margin-right: 2%;
  padding-top: 2em;
  padding-bottom: 2em;
  background: #3d3d3d;
  border-radius: 30px;
}

.no_mem[data-state="false"] {
  display: none;
}

.mem {
  display: block;
  height: 100%;
}

.mem::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.mem {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}

.mem[data-state="false"] {
  display: none;
}

.search_choose {
  width: 100%;
  height: 4em;
  padding: 1em 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 0.5em;
  // border: 1px solid rgba(255, 255, 255, 0.233);
  background: #3d3d3d;
  border-radius: 30px;
}

.search_choose[data-selected="true"] {
  background: #002307;
}

.search_remove_all {
  background-color: #4c0000;
  width: 100%;
  padding: 2em 2em;
  border-radius: 30px;
}

.search_remove_all:hover {
  background-color: #7c0000;
}

// ########################## Queue panel ##########################
.queue {
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0.01em;
  top: 5.5em;
}

.queue[data-state="false"] {
  display: none;
}

.queue_res::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.queue_res {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}

.queue_res[data-state="false"] {
  display: none;
}

.queue_tab {
  width: 100%;
  display: grid;
  // border: 1px solid rgba(255, 255, 255, 0.233);
  background: #222222;
  // border-radius: 30px;
}

.queue_tab[data-number="odd"] {
  background: #363636;
}

.queue_info {
  padding: 1em 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.queue_tab[data-color="true"] {
  background: #003655;
}

.queue_button {
  margin-left: 1em;
}

.queue_res {
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  // margin-top: 0.5em;
  // margin-left: 0.5em;
  background: inherit;
  img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    aspect-ratio: 1/1;
  }
}

.queue_no_mem {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 98%;
  margin-top: 1.5em;
  margin-left: 1%;
  margin-right: 2%;
  padding-top: 2em;
  padding-bottom: 2em;
  background: #3d3d3d;
  border-radius: 30px;
}

.queue_no_mem[data-state="false"] {
  display: none;
}

.queue_title{
  overflow-x: hidden;
  overflow-y: scroll;
  width: 40%;
  white-space: nowrap;
}

.queue_title::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.queue_title {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}

.queue_none {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 98%;
  margin-top: 1em;
  margin-left: 1%;
  margin-right: 2%;
  padding-top: 2em;
  padding-bottom: 2em;
  background: #3d3d3d;
  border-radius: 30px;
}

.queue_none[data-state="true"] {
  display: none;
}

.queue_remove_all {
  background-color: #4c0000;
  width: 100%;
  padding: 2em 2em;
}

.queue_remove_all:hover {
  background-color: #7c0000;
}

.flex_button {
  display: flex;
  width: 7em;
  justify-content: space-between;
  align-items: center;
}