@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;500;700&display=swap');
@import './breakpoints.scss';

* {
  outline: 1px solid red;
}

html,
body {
  padding: 0;
  margin: 0;
  font-family: 'DM Sans';
  background-color: #1d1d1d;
  height: 100%;
  color: #fff;
}


#__next {
  height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

.page {
  height: 100%;
  background-color: #1d1d1d;
  padding: 50px;
}

.page-aligned-center {
  height: 100%;
  background-color: #1d1d1d;
  padding: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  font-family: 'DM Sans';
}

a{
  text-decoration: none;
  color: inherit;
}
header{
  position: sticky;
  z-index: 30;
  top: 0;
}

// Doc Content
.content {
  display: grid;
  padding: 100px 140px 0px 180px;
  width: 10px;
  height: 480px;
  position: sticky;
  z-index: 0;
  top: 0px;
  bottom: 0px;
  p {
    font-size: 22px;
    width: 200px;
  };
  @include media('<=tablet') {
    display: flex;
    padding: 20px 0px 0px 20px;
    height: 70px;
    width: 100%;
    overflow-x: auto;
    overflow-y: hidden;
    background-color: #000000;
    position: fixed;
    bottom: 0px;
    top: initial;
    p {
      font-size: 22px;
      width: 190px;
    };
  }
}

// Nav bar
nav{
  display: flex;
  padding: 18px;
  justify-content: space-between;
  align-items: center;
  background-color: #000000;
  color: #ffffff;
}
.nav__menu-bar{
  display: flex;
  flex-direction: column;
  row-gap: 6px;
  cursor: pointer;
}
.nav__menu-bar div{
  width: 40px;
  height: 4px;
  background-color: #fff;
  border-radius: 2px;
}
.nav__menu-list{
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 60px;
  width: 288px;
  row-gap: 24px;
  right: -288px;
  padding: 10px 40px;
  transition: all 0.2s;
  min-height: calc(100vh - 60px);
  background-color: #000000;
}
.nav__menu-list.active{
  right: 0;
}
.nav__link{
  font-size: 18px; 
  position: relative;
  transition: all 0.2s;
}

.nav__link:hover{
 font-weight: bold;
}

.center{
  min-height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 1100px) {
  .nav__menu-bar{
    display: none;
  }
  .nav__menu-list{
    position: unset;
    flex-direction: row;
    min-height: fit-content;
    width: fit-content;
    column-gap: 24px;
    align-items: center;
  }
  .nav__link::before{
    content: '';
    cursor: pointer;
    position: absolute;
    width: 0%;
    height: 6px;
    bottom: -16px;
    left: 0;
    background-color: black;
    transition: all 0.2s;
  }
  
  .nav__link:hover:before{
   width: 100%;
  }
}

.server_button {
  font-size: 15px;
  font-family: 'DM Sans';
  background-color: inherit;
  cursor: pointer;
  color: inherit;
  outline: none;
  background: #2d2d2d;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 3%;
  box-sizing: border-box;
  box-shadow: 0px 1px 5px 0px #00000018;
}

.server_button:hover {
  background: #393939;
}
