@import "../../../styles/breakpoints.scss";

.container {
    width: 768px;
    margin: 0 auto;
    @include media('<=tablet') {
        width: 100%;
    }
}

.title {
    font-weight: 400;
}

.p {
    font-weight: 400;
}

.loading {
  padding: 15em 0% 0%;
  justify-content: space-between;
  @include media('<=800px') {
    width: 300px;
    padding: 10em 0% 0%;
  }
}

.header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}