@import "../../../styles/breakpoints.scss";

.layout {
    display: flex;
    @include media('<=tablet') {
        display: grid;
    }
}

.start {
    display: grid;
    padding: 100px;
    p {
        font-size: 20px;
    };
    @include media('<=tablet') {
        display: grid;
        padding: 10px 20px;
    }
}

.markdown {
    border-radius: 10px;
    padding: 10px 10px;
    background-color: #131313;
}

.examples {
    padding: 5px 0px;
}