@import "../../styles/breakpoints.scss";

.container {
  padding: 20px 32px;
  margin: 10px 0;
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #2e2e2e;
  @include media('<=tablet') {
    display: block;
    align-items: center;
    justify-content: center;
  }
}

.container:hover {
  background: #505050;
}

.image{
  border-radius: 50%;
  @include media('<=tablet') {
    display: grid;
  }
}

.text {
  @include media('<=tablet') {
    padding-top: 0.8em;
    text-align: right;
  }
}

.line {
  @include media('<=tablet') {
    margin-top: 0.8em;
    border: 1px solid #434343;
  }
}

.sidebar_image {
  margin-top: 2%;
  border-radius: 50%;
  @include media('<=tablet') {
    display: grid;
  }
}