@import "../../../../styles/breakpoints.scss";

.container {
  display: none;
  background: #000000;
  @include media('<=tablet') {
    display: block;
    width: 100%;
    height: 100%;
    padding-top: 4em;
  }
}

.artwork {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70%;
  width: 100%;

  padding-top: 1em;
}

.bottom {
  width: 100vw;
}

.bottom[control-panel="false"] {
  position: fixed;
  width: 100%;
  bottom: 1em;
  left: auto;
  right: 0;
}

.div_block {
  padding: 4.5em 4.5em;
  height: 9em;
}

.start{ 
  position: absolute;
  overflow-y: hidden;

  height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  
  top: 0;
  right: 0;
  bottom: 0;
  left: auto;
  
}

.start[is-control-player="false"] {
  display: none;
}

.artwork_in_div {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  max-width: 350px;
  max-height: 350px;

  border: 0.5px solid #808080;
  
  border-radius: 10px;
  
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-clip: border-box;  
}

.artwork[data-state="false"] {
  display: none;
}

.center_panel {
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 8em;
  display: flex;
  padding-top: 1%;
  padding-bottom: 1%;
  background: #000000;
  @media only screen and (max-height: 700px) {
    padding-bottom: 0%;
  }
}

.center_panel[data-state="false"] {
  display: none;
}

.panel {
  display: none;
  align-items: center;
  justify-content: space-between;
  @include media('<=tablet') {
    width: 80%;
    display: flex;
  }

  @include media('<=400px') {
    width: 95%;
    display: flex;
  }
}

.title {
  padding-top: 0.2em;
  padding-bottom: 0.2em;
  overflow: scroll;
  white-space: nowrap;
  align-items: center;
  display: flex;
  background-color: #000000;
  @media only screen and (max-height: 700px) {
    padding-top: 0.2em;
    padding-bottom: 0.2em;
  }
}

.title[data-state="false"] {
  display: none;
}

.title {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.title::-webkit-scrollbar {
  display: none;
}

.title_size {
  overflow: scroll;
  white-space: nowrap;
  width: 90%;
  margin-left: 2em;
  margin-right: 2em;
}

.title_size {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.title_size::-webkit-scrollbar {
  display: none;
}

.button_center {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000000;
  height: 60px;
}

.button_center_in_div[is-control-player="true"] {
  display: none;
}

.button_center_in_div {
  display: flex;
  align-items: center;
  justify-content: center;

  position: fixed;
  width: 100%;
  bottom: 0.02em;
  left: auto;
  right: 0;

  background: #000000;
  height: 60px;
}

.grid {
  display: grid;
  overflow: scroll;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 10px;
  width: 100%;
  padding: 0em 1em 1.2em 1em;
  background: #000000;
}

.grid::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.grid {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}

.button {
  padding: 18px 28px;
  background-color: #202020;
  display: grid;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  border-radius: 20px;
  margin: 10px 0;
  box-shadow: 0px 1px 5px 0px #00000018;
}

.search[data-state="false"] {
  display: none;
}

// Search panel
.search[data-state="false"] {
  display: none;
}

.search {
  display: block;
  background: inherit;
  position: absolute;
  top: 9em;
  right: 0;
  bottom: 6em;
  left: 0;
}

.search_panel {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 98%;
  margin-top: 0.5em;
  margin-left: 0.5em;
  select {
    border-radius: 30px 0px 0px 30px;
    padding: 0% 1em;
    color: #ffffff;
    height: 3em;
    font-size: 18px;
    font-family: 'DM Sans';
    background: #313131;
    border: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  input[form-name="search"] {
    justify-content: center;
    align-items: center;
    color: #ffffff;
    width: 80%;
    height: 3em;
    font-size: 18px;
    font-family: 'DM Sans';
    background: #3d3d3d;
    border: 0px;
    padding-left: 1em;
  }
  input[form-name="summit"] {
    justify-content: center;
    align-items: center;
    color: #ffffff;
    height: 3em;
    font-size: 18px;
    font-family: 'DM Sans';
    background: #313131;
    border: 0px;
    padding-left: 1em;
    padding-right: 1em;
    border-radius: 0px 30px 30px 0px;
  }
}

.search_res {
  display: grid;
  height: 90%;
  overflow-y: scroll;
  overflow-x: hidden;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.5em;
  margin-left: 1%;
  margin-right: 2%;
  background: #050505;
  img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    aspect-ratio: 1/1;
  }
}

.search_res[data-state="false"] {
  display: none;
}

.search_none {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 98vw;
  margin-top: 0.5em;
  margin-left: 1%;
  margin-right: 2%;
  padding-top: 1em;
  padding-bottom: 1em;
  background: #1e1e1e;
  border-radius: 30px;
}

.search_none[data-state="true"] {
  display: none;
}

.no_mem {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 98vw;
  margin-top: 0.5em;
  margin-left: 1%;
  margin-right: 2%;
  padding-top: 1em;
  padding-bottom: 1em;
  background: #1e1e1e;
  border-radius: 30px;
}

.no_mem[data-state="false"] {
  display: none;
}

.mem {
  display: block;
  height: 100%;
}

.mem::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.mem {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}

.mem[data-state="false"] {
  display: none;
}

.search_choose {
  width: 97vw;
  height: 4em;
  padding: 1em 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 0.5em;
  // border: 1px solid rgba(255, 255, 255, 0.233);
  background: #1e1e1e;
  border-radius: 30px;
}

.search_choose[data-selected="true"] {
  background: #002307;
}

.search_remove_all {
  background-color: #4c0000;
  width: 100%;
  padding: 2em 2em;
  border-radius: 30px;
}

.search_remove_all:hover {
  background-color: #7c0000;
}

// Queue panel
.queue[data-state="false"] {
 display: none;
}

.queue {
  display: block;
  height: 100%;
  padding-top: 0.5em;
}

.queue::-webkit-scrollbar {
  display: none;
}

.queue {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}

.queue {
  overflow-y: scroll;
  overflow-x: hidden;
  justify-content: space-between;
  padding-bottom: 6.3em;
  // margin-left: 1%;
  // margin-right: 1%;
  background: #050505;
  img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    aspect-ratio: 1/1;
  }
}

.queue_res::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.queue_res {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}

.queue_res[data-state="false"] {
  display: none;
}

.queue_tab {
  width: 97vw;
  height: 10em;
  display: grid;
  margin-top: 0.5em;
  margin-left: 0.5em;
  // border: 1px solid rgba(255, 255, 255, 0.233);
  background-color: #222222;
  border-radius: 30px;
}

.queue_tab:hover {
  background-color: #2b2b2b;
}

.queue_info {
  width: 97vw;
  padding: 1em 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.queue_tab[data-color="true"] {
  background: #002337;
}

.queue_button {
  margin-left: 1em;
}

.queue_remove_all {
  background-color: #4c0000;
  width: 97%;
  margin-left: 0.5em;
  padding: 2em 2em;
  border-radius: 30px;
}

.queue_remove_all:hover {
  background-color: #7c0000;
}