@import "../../../../styles/breakpoints.scss";

.panel {
  position: fixed;
  display: flex;
  bottom: 0;
  left: auto;
  right: 0;
  background-color: #000000;
  width: 100%;
  height: 6em;
  @include media('<=tablet') {
    display: none;
  }
}

.artwork {
  border-radius: 5%;
  border: 3px #ffffff;
}

.artwork_in_div {
  width: 65px;
  height: 65px;
  border-radius: 10%;
  border: 0.5px solid #808080;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-clip: border-box;  
}

.controller {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 6em;
  @include media('<=tablet') {
    height: 4.8em;
  }
}

.info {
  margin-left: 2%;
  width: 30%;
  height: 90%;
  display: flex;
  @include media('<=tablet') {
    width: auto;
    display: none;
  }
}

.picture {
  margin-right: 2%;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  img {
    border-radius: 50%;
    border: 3px #4f4f4f;
    width: 65px;
    height: 65px;
  }
  @include media('<=tablet') {
    display: none;
  }
}

.option {
  display: flex;
  align-items: center;
  margin-right: 2%;
  width: 30%;
  @include media('<=tablet') {
    width: 0%;
    padding-right: 15vw;
  }
}

.extend {
  display: flex;
  align-items: center;
  width: 100%;
  margin-left: 80%;
  @include media('<=tablet') {
    display: none;
  }
}

.hidden_extend {
  display: none;
  @include media('<=tablet') {
    display: flex;
    align-items: center;
    position: absolute;
    right: 0px;
  }
}

.hidden {
  display: flex;
  @include media('<=tablet') {
    display: none;
  }
}

.info_hidden {
  display: flex;
  overflow: scroll;
  white-space: nowrap;
  margin-top: 1.4em;
  justify-content: space-between;
  @include media('<=tablet') {
    display: none;
  }
}

.show {
  display: none;
  @include media('<=tablet') {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.control_player {
  display: flex;
  align-items: center;
  justify-content: center;
}

// .loop {
//   margin-right: 1%;
// }

/* Hide scrollbar for Chrome, Safari and Opera */
.info_hidden::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.info_hidden {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}