@import "../../../styles/breakpoints.scss";

.container {
  width: 1000px;
  margin: 0 auto;
  @include media('<=tablet') {
    width: 100%;
  }
}

.flex {
  display: flex;
  flex-direction: row;
  align-items: center; 
  justify-content: space-between
}

.form {
  padding-top: 0.5em;
  select {
    color: #ffffff;
    margin-top: 0.5em;
    width: 100%;
    height: 3em;
    font-size: 18px;
    font-family: 'DM Sans';
    background: #3f3f3f;
    border: 0px;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0% 2%;
    box-sizing: border-box;
    box-shadow: 0px 1px 5px 0px #00000018;
  }
  
  option:not(:checked) {
    background-color:#1d1d1d;
  }
  
  option:checked{
    background: #1d1d1d;
  }
}

.summit {
  margin-right: 1%;
  font-size: 18px;
  font-family: 'DM Sans';
  background: #3f3f3f;
  color: inherit;
  outline: none;
  border: 0px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 3%;
  box-sizing: border-box;
  box-shadow: 0px 1px 5px 0px #00000018;
}

.inputflex {
  display: flex;
}

.select {
  color: #ffffff;
  margin-top: 0.5em;
  width: 100%;
  height: 3em;
  font-size: 18px;
  font-family: 'DM Sans';
  background: inherit;
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.233);
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0% 1%;
  box-sizing: border-box;
  box-shadow: 0px 1px 5px 0px #00000018;
}

.option {
  color: #00000018;
}