@import "../../styles/breakpoints.scss";

.navbar {
    background-color: #121212;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 70px;
    left: 0; 
    top: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 9999; 
    padding: 0 20px;
    p {
        font-size: 20px;
    };
}

.menu {
    display: flex;
    align-items: center;
    justify-content: 'space-between';
}

.right {
    display: 'flex'; 
    width: '450px'; 
    justify-content: 'space-between';
    align-items: center;
}

.button {
    font-size: 18px;
    font-family: 'DM Sans';
    background-color: #2f2f2f;
    white-space: nowrap;
    color: inherit;
    outline: none;
    // border: 1px solid rgba(255, 255, 255, 0.233);
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: max-content;
    padding: 10px 17px;
    margin: 10px 0;
    border-radius: 30px;
    box-shadow: 0px 1px 5px 0px #00000018;
    span {
        padding-left: 8px;
    }
}

.layout {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 760px;
    padding: 30px 80px;
    p {
        font-size: 20px;
    };
    @include media('<=768px') {
        display: grid;
        padding: 10px 10px;
    }
}

.hero {
    width: 700px; 
    padding: 0px 0px ;
    align-items: center;
    justify-content: space-between;
    @include media('<=768px') {
        width: 100%;
        padding: 10px 30px 0px 20px;
    }
}

.featuresLeft {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 560px;
    padding: 30px 130px;
    p {
        font-size: 20px;
    };
    @include media('<=768px') {
        display: grid;
        align-items: center;
        justify-content: space-between;
        height: 800px;
        padding: 20px 20px 0px 30px;
    }
    
}

.featuresRight {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 560px;
    padding: 30px 130px;
    p {
        font-size: 20px;
    };
    @include media('<=768px') {
        display: grid;
        align-items: center;
        justify-content: space-between;
        height: 800px;
        padding: 20px 40px 0px 50px;
    }
    
}

.featuresSize {
    width: 60%; 
    @include media('<=768px') { 
        width: 100%;
    }
}

.ready {
    align-items: center;
    justify-content: space-between;
    height: 260px;
    padding: 30px 130px;
    p {
        font-size: 20px;
    };
    @include media('<=768px') {
        height: 320px;
        padding: 10px 40px;
    }
}

.credit {
    background-color: #121212;
    align-items: center;
    justify-content: space-between;
    height: 200px;
    padding: 30px 90px;
    p {
        font-size: 20px;
    };
    @include media('<=768px') {
        padding: 10px 40px;
    }
}

.logo {
    width: 70%;
    height: 70%;
    @include media('<=768px') {
        margin-left: auto;
        margin-right: auto;
        padding-left: 12%;
        width: 85%;
        height: 85%;
    }
}

.aiLeft {
    width: 50%;
    height: 50%;
    @include media('<=768px') {
        margin-left: auto;
        margin-right: auto;
        padding: 0 40px;
        width: 100%;
        height: 100%;
    }
}

.aiRight {
    margin-left: 30%;
    width: 60%;
    height: 60%;
    @include media('<=768px') {
        margin-left: auto;
        margin-right: auto;
        margin-left: 0%;
        width: 100%;
        height: 100%;
        padding: 0 30px;
    }
}