@import "../../../styles/breakpoints.scss";

.headers {
  @include media('<=tablet') {
    display: none;
  }
}

.pad {
  display: none;
  @include media('<=tablet') {
    display: block;
    height: 70px;
  }
}

.overflow {
  @include media('<=tablet') {
    padding: 0px 0px 0px 20px;
  };
}

.credit {
  background-color: #000000;
  align-items: center;
  justify-content: space-between;
  height: 200px;
  padding: 30px 90px;
  p {
      font-size: 20px;
  };
  @include media('<=768px') {
      padding: 10px 40px;
  }
}