@import "../../../styles/breakpoints.scss";

.layout {
  display: flex;
  @include media('<=tablet') {
    display: grid;
  }
}

.start {
    display: grid;
    padding: 100px;
    width: 100%;
    p {
        font-size: 20px;
    };
    @include media('<=tablet') {
        display: grid;
        padding: 10px 20px;
    }
}

.table {
  padding: 30px 0px 20px;
  td {
    font-size: 20px;
    padding: 10px 10px;
    @include media('<=tablet') {
      display: grid
    }
  };
  tr {
    display: block;
    border: 0px;
    border-radius: 30px;
    margin-top: 0.5em;
  }
  table {
    background-color: inherit;
    border-collapse: collapse;
    width: 100%;
    @include media('<=tablet') {
      width: 100%;
    }
  }
}

.tdspace {
  width: 200px;
}

.headers {
  background-color: #121212;
}

.trcolor {
  background-color: #3a3a3a;
}