.container {
  position: fixed;
  bottom: 40px;
  left: auto;
  right: 20px;
}

.main_ok {
  margin-right: 1%;
  font-size: 18px;
  font-family: 'DM Sans';
  background-color: rgba(0, 186, 15, 0.469);
  color: inherit;
  outline: none;
  border: 1px solid rgba(0, 186, 15, 0.713);
  border-radius: 4px;
  display: grid;
  align-items: center;
  justify-content: space-between;
  padding: 10px 3%;
  box-sizing: border-box;
  box-shadow: 0px 1px 5px 0px #00000018;
}

.main_error {
  margin-right: 1%;
  font-size: 18px;
  font-family: 'DM Sans';
  background-color: rgba(210, 0, 0, 0.469);;
  color: inherit;
  outline: none;
  border: 1px solid rgba(214, 0, 0, 0.736);
  border-radius: 4px;
  display: grid;
  align-items: center;
  justify-content: space-between;
  padding: 10px 3%;
  box-sizing: border-box;
  box-shadow: 0px 1px 5px 0px #00000018;
}