@import "../../../styles/breakpoints.scss";

.container {
    width: 1000px;
    margin: 0 auto;
    @include media('<=tablet') {
      width: 100%;
    }
}

.title {
    font-weight: 400;
}

.p {
    font-weight: 400;
}

.loading {
  padding: 15em 0% 0%;
  justify-content: space-between;
  @include media('<=800px') {
    width: 300px;
    padding: 10em 0% 0%;
  }
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 10px;
  @include media('<=phone') {
    grid-template-columns: 1fr;
  }
}

.card {
  padding: 18px 28px;
  background-color: #313131;
  display: grid;
  align-items: center;
  box-sizing: border-box;
  border-radius: 20px;
  margin: 10px 0;
  box-shadow: 0px 1px 5px 0px #00000018;
}

.button {
  font-size: 18px;
  font-family: 'DM Sans';
  background-color: #454545;
  color: inherit;
  outline: none;
  border: 0px;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 40%;
  box-sizing: border-box;
  margin: 10px 0;
  box-shadow: 0px 1px 5px 0px #00000018;
}

.flex {
  padding-top: 1.5em;
  padding-bottom: 0.5em;
  display: flex;
  flex-direction: row;
  align-items: center; 
  justify-content: space-between
}