@import "../../../../styles/breakpoints.scss";

// Hidden select
.container {
  display: none;
  @include media('<=tablet') {
    display: flex;
    width: 100%;
    height: 4.6em;
    position: fixed;
    overflow: scroll;
    top: 4.45em;
    right: 0;
    left: auto;
    bottom: 0;
    background-color: #000000;
    justify-content: center;
    align-items: center;

    select {
      height: 90%;

      padding: 0% 1em;
      color: #ffffff;
      height: 3em;
      font-size: 18px;
      font-family: 'DM Sans';
      background: #313131;
      border: 0px;

      border-radius: 30px;
      width: 80%;
    }
  }
}

.image {
  display: none;
  @include media('<=tablet') {
    display: flex;
    width: 100%;
    height: 4.6em;
  }
}

.container::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.container {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.backbutton {
  width: 3.5em;
  height: 3.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #2e2e2e;
}

.backbutton_space {
  padding-right: 0.5em;
}