@import "../../../../styles/breakpoints.scss";

.container {
  display: grid;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  img {
    margin-top: 0.6em;
    width: 73px;
    height: 73px;
    // width: 100%;
    // height: 100%;
    border-radius: 40%;
  }
  @include media('<=tablet') {
    img {
      margin-top: 0em;
      margin-left: 0.6em;
    }
  }
}

.container[data-color="yes"] {
  img {
    border: 3px solid #ffffff;
  }
}

.container:hover {
  img {
    border: 3px solid #ffffff;
  }
}

.container[data-color="yes"]:hover {
  img {
    border: 4px solid #ffffff;
  }
}