@import "../../styles/breakpoints.scss";

.button {
  font-size: 18px;
  font-family: 'DM Sans';
  background-color: inherit;
  color: inherit;
  outline: none;
  border: 0px;
  border-radius: 10px;
  background-color: #282828;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 330px;
  padding: 14px 50px;
  box-sizing: border-box;
  margin: 10px 0;
  box-shadow: 0px 1px 5px 0px #00000018;
}

.button:hover {
  background-color: #3f3f3f;
}

.loginpage {
  height: 100%;
  padding: 20em 0% 0%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  @include media('<=800px') {
    padding: 15em 0% 0%;
  }
}

.footer {
  display: flex;
  width: 400px;
  padding: 15em 0% 0%;
  justify-content: space-between;
  @include media('<=800px') {
    width: 300px;
    padding: 10em 0% 0%;
  }
}